export class Knight {  
    
    isMoving = false
  
    health = 100

    
    constructor(
      posX,
      posY,
      speed,
      player
    ) {
      this.makeKnight(posX, posY)
      this.stateInit()
      this.enableVunerability()
      this.speed = speed
      this.player = player
      // this.update()
    }
  
    makeKnight(x, y) {
      
      this.initialX = x
      this.initialY = y
      this.gameObj = add([
        sprite("knight", { anim: "idle" }),
        area({ shape: new Polygon([vec2(-19,-24), vec2(-19,92), vec2(19,92), vec2(19,-24)]) }),
        scale(2),
        anchor("center"),
        pos(x, y),
        body(),
        health(this.health),
        state("move", ["fastAttack", "chargedAttack", "move", "idle", "die"]),
        "knight",
      ])
      const hitBox = this.gameObj.add([
          pos(0,0),
          polygon(
                [vec2(-57,3), vec2(-135,18), vec2(-111,64), vec2(-44,50), vec2(44,50), vec2(111,64), vec2(135,18), vec2(57,3)] 
              ),
          opacity(0),
          area(),
          "knight-hitbox",
        ])
      // this.fastHitboxLeft = this.gameObj.add([
      //   pos(0,0),
      //   polygon(
      //     [vec2(-20,7), vec2(-136,18), vec2(-20,63)] 
      //   ),
      //   rotate(0),
      //   opacity(0.5),
      //   area(),
      //   "knight-hitbox",
      //   "knight-fast-hitbox-left"
      // ])
      // this.fastHitboxRight = this.gameObj.add([
      //   pos(0,0),
      //   polygon(
      //     [vec2(20,7), vec2(136,18), vec2(20,63)]
      //   ),
      //   area(),
      //   opacity(0.5),
      //   area(),
      //   "knight-hitbox",
      //   "knight-fast-hitbox-right"
      // ])
      // this.chargedHitboxRight = this.gameObj.add([
      //   pos(0,0),
      //   polygon(
      //     [vec2(-57,-79), vec2(-14,-87), vec2(30,-81), vec2(82,-49), vec2(110,8), vec2(112,65), vec2(42,57)]
      //   ),
      //   area(),
      //   color(0, 0, 255),
      //   opacity(0.5),
      //   area(),
      //   "knight-hitbox",
      //   "knight-charged-hitbox-right"
      // ])
      // this.chargedHitboxLeft = this.gameObj.add([
      //   pos(0,0),
      //   polygon(
      //     [vec2(57,-79), vec2(14,-87), vec2(-30,-81), vec2(-82,-49), vec2(-110,8), vec2(-112,65), vec2(-42,57)]
      //   ),
      //   area(),
      //   color(0, 0, 255),
      //   opacity(0.5),
      //   area(),
      //   "knight-hitbox",
      //   "knight-charged-hitbox-left"
      // ])
    }

    // stateInit() {
    //     this.gameObj.onStateEnter("move", async () => {
    //         await wait(1);
    //         this.isMoving = true
    //         if (this.gameObj.checkCollision(this.player.gameObj.get("attack-area")[0])) {
    //           console.log('collides')
    //           this.gameObj.enterState("fastAttack")
    //         } else {
    //           console.log('else')
    //           this.gameObj.enterState("move")
    //         }
    //     });

    stateInit() {
          this.gameObj.onStateEnter("move", async () => {
              await wait(1);
              this.isMoving = true
          });
        
        this.gameObj.onStateUpdate("move", () => {

            if (this.gameObj.curAnim() !== "run") this.gameObj.play("run")
            if (!this.player.gameObj.exists()) return;
            const dir = this.player.gameObj.pos.sub(this.gameObj.pos).unit();
            if (dir.x < 0) {
                this.gameObj.flipX = true
            } else if (dir.x > 0) {
                this.gameObj.flipX = false
                
            }
            this.gameObj.move(dir.scale(200));

            if (this.gameObj.checkCollision(this.player.gameObj.get("attack-area")[0])) {

              this.gameObj.enterState("fastAttack")
            } else {
              this.gameObj.enterState("move")
            }
        });

        this.gameObj.onStateEnter("fastAttack", async () => {
          this.gameObj.play("fast")
          if (this.gameObj.checkCollision(this.player.gameObj.get("attack-area")[0])) {
            if (this.gameObj.get("knight-hitbox")[0].checkCollision(this.player.gameObj)) {
              this.player.gameObj.hurt(1)
              shake(5)
            }
            // if (this.gameObj.flipX === true) {
            //   if (this.gameObj.get("knight-fast-hitbox-left")[0].checkCollision(this.player.gameObj)) {
            //     this.player.gameObj.hurt(5)
            //     shake(5)
            //   }
            // } else if (this.gameObj.flipX === false) {
            //   if (this.gameObj.get("knight-fast-hitbox-right")[0].checkCollision(this.player.gameObj)) {
            //     this.player.gameObj.hurt(5)
            //     shake(5)
            //   }
            // }
          }
          await wait(0.3)
          this.gameObj.enterState("chargedAttack")
        });        

        this.gameObj.onStateEnter("chargedAttack", async () => {
          this.gameObj.play("charged")
          if (this.gameObj.checkCollision(this.player.gameObj.get("attack-area")[0])) {
            if (this.gameObj.get("knight-hitbox")[0].checkCollision(this.player.gameObj)) {
              this.player.gameObj.hurt(5)
              shake(10)
            }
            // if (this.gameObj.flipX === true) {
            //   if (this.gameObj.get("knight-charged-hitbox-left")[0].checkCollision(this.player.gameObj)) {
            //     this.player.gameObj.hurt(10)
            //     shake(10)
            //   }
            // } else if (this.gameObj.flipX === false) {
            //   if (this.gameObj.get("knight-charged-hitbox-right")[0].checkCollision(this.player.gameObj)) {
            //     this.player.gameObj.hurt(10)
            //     shake(10)
            //   }
            // }
          }
          await wait(0.5)
          this.gameObj.enterState("move")
        });
        
        this.gameObj.onStateEnter("die", () => {

          if (this.gameObj.curAnim() !== "fall") this.gameObj.play("fall", {
            onEnd: async() => {
              
              destroy(this.gameObj)
              this.player.killCount +=1
              
            }
          })
        });
    }

    enableVunerability() {
        this.gameObj.onCollide("fast-hitbox", (hitbox) => {
          this.gameObj.hurt(20);
          this.gameObj.applyImpulse(vec2(this.gameObj.flipX ? 1000 : -1000, 0))
          wait(.1, () => {
            this.gameObj.applyImpulse(vec2(-this.gameObj.vel.x, -this.gameObj.vel.y))
          });
        })
        this.gameObj.onCollide("charged-hitbox", (hitbox) => {
          this.gameObj.hurt(50);
          this.gameObj.applyImpulse(vec2(this.gameObj.flipX ? 1500 : -1500, 0))
          wait(.2, () => {
            this.gameObj.applyImpulse(vec2(-this.gameObj.vel.x, -this.gameObj.vel.y))
          });
        })
        
      
        this.gameObj.onDeath(() => {
          this.gameObj.enterState("die");
        })
    }

    update() {
      onUpdate(() => {
        this.fastHitboxLeft.pos.x = this.gameObj.pos.x
        this.fastHitboxRight.pos.x = this.gameObj.pos.x 
        this.chargedHitboxRight.pos.x = this.gameObj.pos.x 
        this.chargedHitboxLeft.pos.x = this.gameObj.pos.x
        this.fastHitboxLeft.pos.y = this.gameObj.pos.y
        this.fastHitboxRight.pos.y = this.gameObj.pos.y 
        this.chargedHitboxRight.pos.y = this.gameObj.pos.y 
        this.chargedHitboxLeft.pos.y = this.gameObj.pos.y
      })
    }
    

    

}